import React, { useState, useEffect } from 'react';
import { initializeApp } from 'firebase/app';
import { getDatabase, ref, set } from 'firebase/database';
import { getAuth, signInWithEmailAndPassword } from 'firebase/auth';
import { getStorage, ref as storageRef, uploadBytesResumable, getDownloadURL } from "firebase/storage";

const firebaseConfig = {
  apiKey: "AIzaSyCgpGMBNGohDrCxa1MCsNHKqoaWMAb1sD8",
  authDomain: "talkbox-23c25.firebaseapp.com",
  databaseURL: "https://talkbox-23c25-default-rtdb.firebaseio.com",
  projectId: "talkbox-23c25",
  storageBucket: "talkbox-23c25.appspot.com",
  messagingSenderId: "83481544518",
  appId: "1:83481544518:web:17dd8571c7612851c28747",
  measurementId: "G-JTKB93HFP9"
};

// Initialize Firebase
const app = initializeApp(firebaseConfig);
const db = getDatabase(app);
const auth = getAuth(app);
const storage = getStorage(app); // Initialize Firebase Storage

// --- HARDCODED CREDENTIALS (FOR DEMONSTRATION ONLY - SECURITY RISK!) ---
const FIREBASE_USER_EMAIL = "tnmesserges@gmail.com";
const FIREBASE_USER_PASS = "Messerges";
// --- END HARDCODED CREDENTIALS ---

// Define emojis based on the provided emoji_definitions.h
const EMOJI_MAP = [
  { text: "<3", emoji: "" }, // EMOJI_HEART
  { text: ":)", emoji: "" }, // EMOJI_SMILE
  { text: ":(", emoji: "" }, // EMOJI_SAD
  { text: "*", emoji: "" },  // EMOJI_STAR
  { text: "@", emoji: "" }, // EMOJI_MAIL
  { text: "LB", emoji: "" }, // EMOJI_LADYBUG
  { text: "XD", emoji: "" }, // EMOJI_LAUGHING_TEARS
  { text: "0)", emoji: "" }, // EMOJI_ANGEL
  { text: "B)", emoji: "" }, // EMOJI_COOL
  { text: ";)", emoji: "" }, // EMOJI_WINK
  { text: ":D", emoji: "" }, // EMOJI_HEART_EYES
  { text: ":=(", emoji: "" }, // EMOJI_CRYING
  { text: ":'(", emoji: "" }, // EMOJI_DOWNCAST_SWEAT
  { text: ";S", emoji: "" }, // EMOJI_WINKY_KISS
  { text: ";P", emoji: "" }, // EMOJI_SMIRK
  { text: ":O", emoji: "" }  // EMOJI_SURPRISED
];

export default function App() {
  const [message, setMessage] = useState('');
  const [boardId, setBoardId] = useState('board1');
  const [status, setStatus] = useState('Initializing...');
  const [isAuthenticated, setIsAuthenticated] = useState(false);
  const [imageFile, setImageFile] = useState(null); // State for the image file
  const [uploadProgress, setUploadProgress] = useState(0); // State for upload progress

  // Authenticate user when component mounts
  useEffect(() => {
    const authenticateUser = async () => {
      try {
        setStatus('Attempting to sign in...');
        await signInWithEmailAndPassword(auth, FIREBASE_USER_EMAIL, FIREBASE_USER_PASS);
        setIsAuthenticated(true);
        setStatus('Signed in successfully.');
        console.log('Firebase user signed in:', auth.currentUser.uid);
      } catch (error) {
        setIsAuthenticated(false);
        setStatus('Authentication failed. Check console for details.');
        console.error('Firebase Authentication Error:', error.message);
      }
    };

    authenticateUser();
  }, []);

  const sendMessage = async () => {
    if (!message) {
      setStatus('Message cannot be empty.');
      return;
    }
    if (!isAuthenticated) {
      setStatus('Not authenticated. Please wait or check authentication status.');
      return;
    }

    try {
      const messageRef = ref(db, `messages/${boardId}`);
      await set(messageRef, message);

      setStatus(`Message sent to ${boardId}!`);
      setMessage('');
    } catch (error) {
      setStatus('Error sending message');
      console.error('Firebase Write Error:', error);
    }
  };

  const handleImageUpload = async () => {
    if (!imageFile) {
      setStatus("Please select an image to upload.");
      return;
    }
    if (!isAuthenticated) {
      setStatus("You must be signed in to upload an image.");
      return;
    }

    const storagePath = `images/${boardId}/${new Date().getTime()}_${imageFile.name}`;
    const imageStorageRef = storageRef(storage, storagePath);
    const uploadTask = uploadBytesResumable(imageStorageRef, imageFile);

    uploadTask.on('state_changed',
      (snapshot) => {
        const progress = (snapshot.bytesTransferred / snapshot.totalBytes) * 100;
        setUploadProgress(progress);
        setStatus(`Uploading: ${Math.round(progress)}%`);
      },
      (error) => {
        console.error("Upload Error:", error);
        setStatus("Error uploading image. See console for details.");
      },
      async () => {
        const downloadURL = await getDownloadURL(uploadTask.snapshot.ref);
        console.log("File available at", downloadURL);

        try {
          const messageRef = ref(db, `messages/${boardId}`);
          await set(messageRef, downloadURL);
          setStatus(`Image sent to ${boardId}!`);
          setImageFile(null); // Clear the selected file
          setUploadProgress(0); // Reset progress
        } catch (error) {
          setStatus('Error sending image URL to database.');
          console.error('Firebase Write Error:', error);
        }
      }
    );
  };

  const addEmojiToMessage = (emojiText) => {
    setMessage((prevMessage) => prevMessage + emojiText);
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-blue-50 to-blue-200 flex flex-col items-center justify-center p-4 font-sans relative overflow-hidden">
      {/* Super Cool Animated Background Blobs */}
      <div className="absolute top-0 left-0 w-80 h-80 bg-blue-300 rounded-full mix-blend-multiply filter blur-2xl opacity-70 animate-super-blob animation-delay-0"></div>
      <div className="absolute top-1/4 right-0 w-96 h-96 bg-pink-200 rounded-full mix-blend-multiply filter blur-2xl opacity-70 animate-super-blob animation-delay-2000"></div>
      <div className="absolute bottom-0 left-1/4 w-72 h-72 bg-purple-200 rounded-full mix-blend-multiply filter blur-2xl opacity-70 animate-super-blob animation-delay-4000"></div>
      <div className="absolute bottom-1/4 right-1/4 w-64 h-64 bg-green-200 rounded-full mix-blend-multiply filter blur-2xl opacity-70 animate-super-blob animation-delay-6000"></div>

      <h1 className="text-5xl md:text-6xl font-extrabold text-blue-700 mb-8 z-10 relative animate-fade-in-down drop-shadow-xl text-center">
        <span className="text-pink-400 animate-pulse-emoji inline-block"></span> TalkBox Express <span className="text-pink-400 animate-pulse-emoji inline-block"></span>
      </h1>
      <div className="bg-white p-8 md:p-10 rounded-3xl shadow-2xl w-full max-w-lg transform transition-all duration-700 hover:scale-[1.02] animate-pop-in z-10 border-4 border-blue-300 relative overflow-hidden">
        {/* Subtle inner decorative circles */}
        <div className="absolute -top-10 -right-10 w-24 h-24 bg-blue-100 rounded-full opacity-50 animate-spin-slow"></div>
        <div className="absolute -bottom-8 -left-8 w-20 h-20 bg-pink-100 rounded-full opacity-50 animate-spin-slow animation-delay-1500"></div>

        <p className={`text-sm mb-4 font-medium ${isAuthenticated ? 'text-green-600' : 'text-red-600'}`}>
          Auth Status: {status}
        </p>

        <div className="mb-6 relative">
          <label htmlFor="boardSelect" className="block text-gray-700 text-sm font-bold mb-2">Choose Your TalkBox:</label>
          <select
            id="boardSelect"
            value={boardId}
            onChange={(e) => setBoardId(e.target.value)}
            className="w-full p-3 border-2 border-blue-200 rounded-lg focus:outline-none focus:ring-4 focus:ring-blue-300 transition duration-300 transform hover:scale-[1.01] active:border-blue-400 bg-white shadow-sm appearance-none cursor-pointer animate-float-input"
          >
            <option value="board1">Sweet TalkBox 1 (board1)</option>
            <option value="box2">Dreamy TalkBox 2 (box2)</option>
          </select>
          <span className="absolute right-3 top-1/2 -translate-y-1/2 text-blue-400 pointer-events-none"></span>
        </div>

        <div className="mb-6 relative">
          <label htmlFor="messageInput" className="block text-gray-700 text-sm font-bold mb-2">Send a Cute Message:</label>
          <input
            id="messageInput"
            type="text"
            value={message}
            onChange={(e) => setMessage(e.target.value)}
            className="w-full p-4 border-2 border-pink-200 rounded-xl focus:outline-none focus:ring-4 focus:ring-pink-300 transition duration-300 transform hover:scale-[1.01] active:border-pink-400 bg-white placeholder-pink-300 shadow-sm animate-float-input animation-delay-300"
            disabled={!isAuthenticated}
            placeholder="Type your lovely message here..."
          />
        </div>

        <div className="mb-8">
          <label className="block text-gray-700 text-sm font-bold mb-3">Add Sparkle with Emojis:</label>
          <div className="grid grid-cols-6 gap-3">
            {EMOJI_MAP.map((emojiItem, index) => (
              <button
                key={index}
                onClick={() => addEmojiToMessage(emojiItem.text)}
                className="p-3 bg-blue-100 rounded-full hover:bg-blue-200 transition duration-300 text-3xl flex items-center justify-center transform hover:scale-125 active:scale-90 shadow-md hover:shadow-lg focus:outline-none focus:ring-2 focus:ring-blue-400 animate-wiggle-emoji"
                disabled={!isAuthenticated}
                title={emojiItem.text}
              >
                {emojiItem.emoji}
              </button>
            ))}
          </div>
        </div>

        <div className="flex space-x-4 mb-6">
          <button
            onClick={sendMessage}
            className={`w-full px-8 py-4 rounded-full text-xl font-bold transition duration-300 ease-in-out transform hover:scale-105 active:scale-95 shadow-xl hover:shadow-2xl animate-pulse-send
              ${isAuthenticated
                ? 'bg-blue-400 hover:bg-blue-500 text-white'
                : 'bg-gray-300 text-gray-600 cursor-not-allowed'
              }`}
            disabled={!isAuthenticated}
          >
            Send My Message! <span className="ml-2 animate-bounce-heart inline-block"></span>
          </button>
        </div>

        {/* --- Image Upload Section --- */}
        <div className="border-t-2 border-blue-200 pt-8 mt-8">
          <label className="block text-gray-700 text-sm font-bold mb-3">Or Share a Pretty Picture:</label>
          <input
            type="file"
            onChange={(e) => setImageFile(e.target.files[0])}
            className="w-full text-sm text-blue-600
              file:mr-4 file:py-3 file:px-6
              file:rounded-full file:border-0
              file:text-md file:font-semibold
              file:bg-pink-100 file:text-pink-700
              hover:file:bg-pink-200 transition duration-300 cursor-pointer animate-float-input animation-delay-600"
            disabled={!isAuthenticated}
            accept="image/jpeg, image/png, image/gif"
          />
          {uploadProgress > 0 && (
            <div className="w-full bg-blue-100 rounded-full h-3 mt-4 overflow-hidden">
              <div className="bg-blue-400 h-full rounded-full transition-all duration-500 animate-progress-fill" style={{ width: `${uploadProgress}%` }}></div>
            </div>
          )}
          <button
            onClick={handleImageUpload}
            className={`w-full mt-6 px-8 py-4 rounded-full text-xl font-bold transition duration-300 ease-in-out transform hover:scale-105 active:scale-95 shadow-xl hover:shadow-2xl animate-pulse-upload
              ${isAuthenticated
                ? 'bg-green-400 hover:bg-green-500 text-white'
                : 'bg-gray-300 text-gray-600 cursor-not-allowed'
              }`}
            disabled={!isAuthenticated || !imageFile}
          >
            Upload My Image! <span className="ml-2 animate-bounce-star inline-block"></span>
          </button>
        </div>

        {status && <p className="mt-8 text-center text-blue-600 font-semibold animate-fade-in">{status}</p>}
      </div>
    </div>
  );
}